a {
    color: #fff !important;
}

p {
    margin: 0;
}

.header {
    width: 100%;
    height: 100vh;
    background: linear-gradient(rgba(16,29,44,.95), rgba(16,29,44,.95)), url("../assets/header-bg.jpg") center no-repeat;
    background-size: cover;
    position: relative;
}

header > .container {
    position: relative;
    height: 100%;
    padding: 0%;
}

.navbar-brand {
    position: absolute;
    width: 100%;
    max-width: 100px;
    top: 10px;
    left: 50%;
    transform: translateX(-50%);
    transition: ease-in 650ms;
    cursor: pointer;
}

.navbar-brand:hover {
    opacity: .8;
}

.hamburger-menu{
    position: fixed;
    top: 25px;
    right: 15px;
    width: 50px;
    height: 50px;
    display: flex;
    background-color: #101D2C;
    border-radius: 4px;
    cursor: pointer;
    z-index: 999;
}

.hamburger-menu i {
    font-size: 30px;
    color: rgba(198, 153, 99, .7);
    margin: auto;
}


.goodbye {
    display: none;
}

.hello {
    display: block;
}

.nav-list{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    list-style: none;
    background-color: #101D2C;
    display: flex;
    flex-direction: column;
    justify-content: center;
    opacity: 0;
    transform: scale(1); 
    transition: ease-in 650ms;
    overflow: hidden;
}

.nav-list-open {
    opacity: 1;
    transform: scale(1);
    z-index: 900;
}

.hero-text {
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -45%);
    text-align: center;
}

.hero-text h1 {
    font-family: "Niconne", cursive;
    color: #c69963;
}

.btn{
    width: 100px;
    padding: 5px 0 !important;
    border: 1px solid #c69963;
    position: relative;
    overflow: hidden;
}

.btn::before {
    content: "";
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(120deg, transparent, rgba(255, 255, 255, .3), transparent);
    transition: all 650ms;
}

.btn:hover::before{
    left: 100%;
}

@media (min-width: 576px) {
    .navbar-brand{
        max-width: 150px;
    }

    .hamburger-menu{
        top: 35px;
        right: 25px;
    }

}

@media (min-width: 768px) {
    .btn{
        width: 150px;
    }
}

@media (min-width: 992px) {
    .header > .container {
        padding: 0 15px;
    }

    .hamburger-menu{
        display: none;
    }

    .navbar-brand{
        position: initial;
        transform: initial;
    }

    .nav-list{
        position: initial;
        width: initial;
        height: initial;
        background-color: initial;
        flex-direction: row;
        opacity: initial;
        transform: initial;
        transition: initial;
        margin: 0;
        padding: 0;
    }

    .nav-item{
        margin: 0 30px;
    }

    .nav-item:last-child {
        margin-right: 0;
    }

    .nav-link{
        position: relative;
        padding: 0;
    }

    .nav-link::before{
        content: "";
        position: absolute;
        left: 0;
        background-color: #c69963;
        width: 100%;
        height: 2px;
        bottom: -3px;
        transform: scaleX(0);
        transform-origin: center;
        transition: ease-in 650ms;
    }

    .nav-link:hover::before {
        transform: scaleX(1);
    }
}

.nav-link:hover {
    cursor: pointer;
    color: #c69963 !important;
}

